/**
 * HOOK CUSTOMIZADO: useContent
 * 
 * Este hook gerencia o contedo (textos e imagens) do site.
 * Ele busca o contedo da API para uma seo especfica.
 * 
 * Como usar:
 * import { useContent } from './hooks/useContent';
 * 
 * function MeuComponente() {
 *   const { content, loading } = useContent('hero');
 *   if (loading) return <div>Carregando...</div>;
 *   return <h1>{content.title}</h1>;
 * }
 */

import { useState, useEffect } from 'react';
import {
    fetchHeader,
    fetchHero,
    fetchAbout,
    fetchServices,
    fetchTeam,
    fetchFooter,
    fetchWhatsapp,
    fetchSituations,
    fetchServiceWork
} from '../services/api';

// Mapeamento de sees para suas respectivas funes de busca
const contentFetchers = {
    header: fetchHeader,
    hero: fetchHero,
    about: fetchAbout,
    services: fetchServices,
    team: fetchTeam,
    footer: fetchFooter,
    whatsapp: fetchWhatsapp,
    situations: fetchSituations,
    serviceWork: fetchServiceWork
};

/**
 * Hook para buscar contedo de uma seo especfica
 * @param {string} section - Nome da seo ('header', 'hero', 'about', etc.)
 */
export const useContent = (section) => {
    // Estado para armazenar o contedo
    const [content, setContent] = useState(null);

    // Estado para indicar se est carregando
    const [loading, setLoading] = useState(true);

    // Estado para armazenar erros
    const [error, setError] = useState(null);

    // useEffect executa quando o componente  montado ou quando 'section' muda
    useEffect(() => {
        // Funo para carregar o contedo
        const loadContent = async () => {
            try {
                setLoading(true);

                // Busca a funo correta para a seo
                const fetcher = contentFetchers[section];

                if (!fetcher) {
                    throw new Error(`Seo '${section}' no encontrada`);
                }

                // Busca o contedo da API
                const data = await fetcher();
                setContent(data);
                setError(null);
            } catch (err) {
                if (import.meta.env.DEV) {
                    console.error(`Erro ao carregar contedo da seo ${section}:`, err);
                }
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        loadContent();
    }, [section]); // Recarrega se a seo mudar

    // Retorna o contedo, estado de loading e erro
    return { content, loading, error };
};