/**
 * COMPONENTE: SkipLink
 * 
 * Link de pular para contedo principal.
 * Melhora acessibilidade para usurios de teclado e screen readers.
 */

import './SkipLink.css';

function SkipLink() {
    return (
        <a href="#main-content" className="skip-link">
            Pular para o contedo principal
        </a>
    );
}

export default SkipLink;
