/**
 * COMPONENTE: NotFound (Pgina 404)
 * 
 * Pgina exibida quando o usurio tenta acessar uma rota que no existe.
 * Design simples e elegante seguindo o padro visual do projeto.
 */

import { Link } from 'react-router-dom';
import './NotFound.css';

function NotFound() {
    return (
        <div className="not-found">
            <div className="not-found-content">
                <div className="not-found-number">404</div>
                <h1 className="not-found-title">Pgina No Encontrada</h1>
                <p className="not-found-description">
                    Desculpe, a pgina que voc est procurando no existe ou foi movida.
                </p>
                <Link to="/" className="not-found-button">
                    Voltar para o Incio
                </Link>
            </div>
        </div>
    );
}

export default NotFound;
