/**
 * COMPONENTE: Services
 * 
 * Seo de "reas de Atuao" / "Servios".
 * Mostra os servios oferecidos pelo escritrio em cards.
 * 
 * O contedo vem da API atravs do hook useContent.
 */

import { useContent } from '../hooks/useContent';
import dynamicIconImports from 'lucide-react/dynamicIconImports'; // Importao dinmica
import { motion } from 'framer-motion';
import { lazy, Suspense, useMemo } from 'react';
import './Services.css';

// Componente para renderizar cone de forma dinmica
const DynamicIcon = ({ name, ...props }) => {
    const LucideIcon = useMemo(() => {
        // Converte PascalCase para kebab-case (ex: Briefcase -> briefcase, Building2 -> building-2)
        const iconName = name
            .replace(/([a-z0-9])([A-Z])/g, '$1-$2')
            .toLowerCase();

        const dynamicIcon = dynamicIconImports[iconName];

        if (!dynamicIcon) {
            if (import.meta.env.DEV) {
                console.warn(`cone "${name}" (convertido para "${iconName}") no encontrado em lucide-react.`);
            }
            return lazy(dynamicIconImports['briefcase']); // Fallback
        }

        return lazy(dynamicIcon);
    }, [name]);

    return (
        <Suspense fallback={<div style={{ width: 56, height: 56, background: '#f0f0f0', borderRadius: '50%' }} />}>
            <LucideIcon {...props} />
        </Suspense>
    );
};

function Services() {
    // Busca o contedo da seo services da API
    const { content, loading } = useContent('services');

    // Enquanto carrega, mostra um placeholder
    if (loading || !content) {
        return (
            <section id="services" className="services section">
                <div className="container">
                    <div className="spinner"></div>
                </div>
            </section>
        );
    }

    // Se content  um array, usa diretamente; seno, pega content.services
    const services = Array.isArray(content) ? content : (content.services || []);
    const title = content.title || 'reas de Atuao';
    const subtitle = content.subtitle || 'Solues Jurdicas Completas';

    return (
        <section id="services" className="services section">
            <div className="container">
                {/* Cabealho da seo */}
                <div className="section-header">
                    <p className="section-subtitle">{subtitle}</p>
                    <h2 className="section-title">{title}</h2>
                </div>

                {/* Grid de servios */}
                <div className="services-grid">
                    {services.map((service, index) => {
                        return (
                            <motion.div
                                key={service.id}
                                className="service-card"
                                initial={{ opacity: 0, y: 50 }} // Comea invisvel e abaixo
                                whileInView={{ opacity: 1, y: 0 }} // Aparece ao entrar na viewport
                                viewport={{ once: true, amount: 0.2 }}
                                transition={{ duration: 0.5, delay: index * 0.1 }} // Delay escalonado
                                whileHover={{ y: -8, boxShadow: "0 20px 25px -5px rgba(0, 0, 0, 0.1)" }} // Levanta ao passar o mouse
                            >
                                {/* cone do servio */}
                                <motion.div
                                    className="service-icon"
                                    whileHover={{ scale: 1.1, rotate: 5 }} // Animao do cone
                                    transition={{ duration: 0.3 }}
                                >
                                    {/* Renderiza o cone via componente dinmico */}
                                    <DynamicIcon name={service.icon} size={56} strokeWidth={1.5} />
                                </motion.div>

                                {/* Ttulo do servio */}
                                <h3 className="service-title">{service.title}</h3>

                                {/* Descrio do servio */}
                                <p className="service-description">{service.description}</p>

                                {/* Lista de caractersticas/features */}
                                <ul className="service-features">
                                    {service.features.map((feature, idx) => (
                                        <motion.li
                                            key={idx}
                                            className="service-feature"
                                            initial={{ opacity: 0, x: -10 }}
                                            whileInView={{ opacity: 1, x: 0 }}
                                            viewport={{ once: true }}
                                            transition={{ duration: 0.3, delay: 0.5 + (idx * 0.05) }}
                                        >
                                            <span className="feature-bullet"></span>
                                            {feature}
                                        </motion.li>
                                    ))}
                                </ul>
                            </motion.div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
}

export default Services;